import {Message} from "element-ui";
import {operaStorage} from '@/utils/tools'

export default {
  namespaced: true,
  state: ()=>({
    token: operaStorage('get', 'Authorization'),
    userInfo: JSON.parse(operaStorage('get', 'userInfo')||'{}')
  }),
  actions: {
    userLogin({commit}, args) {
      commit('UserLogin', args)
    }
  },
  mutations: {
    UserLogin(state, args) {
      let {loginForm, login} = args
      login(loginForm).then(res => {
        if (res.data.code == 1) {
          let {userInfo, token} = res.data.data
          operaStorage('set','Authorization',token)
          operaStorage('set','userInfo',JSON.stringify(userInfo))
          state.token = token
          state.userInfo = userInfo
          return true
        } else Message.error(res.data.msg)
      })
    }
  }
}
